<template>
  <div class="exam-page">
    <van-form @submit="onSubmit">
      <div class="question" v-for="(question, index) in questionList" :key="question.id">
        <div class="title">{{ index + 1 + ". " + question.title }}</div>
        <div class="title">{{ "题目id: " + question.id }}</div>
        <van-field name="radio" label="" :rules="[{ required: true, message: '请选择' }]">
          <template #input>
            <van-radio-group v-model="question.selected" :disabled="isSubmit">
              <van-radio
                v-for="option in question.options"
                :key="option.name"
                :name="option.name"
                :class="{
                  'selected-option': isSubmit && option.name === question.selected,
                  'answer-option': isSubmit && option.name === question.answer
                }"
                >{{ option.name + ". " + option.label }}</van-radio
              >
            </van-radio-group>
          </template>
        </van-field>
      </div>
      <div style="margin: 16px;">
        <van-button v-if="!isSubmit" round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
    <van-button v-if="isSubmit" round block type="danger" native-type="button" @click="init"
      >重新作答</van-button
    >
  </div>
</template>

<script>
import cloneDeep from "lodash/cloneDeep";
import shuffle from "lodash/shuffle";
import { questionCollection } from "./questionCollection";
export default {
  name: "Exam",
  data() {
    return {
      questionList: [],
      isSubmit: false
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      const newQuestionCollection = shuffle(cloneDeep(questionCollection));
      this.isSubmit = false;
      this.questionList = newQuestionCollection.slice(0, 5);
    },
    onSubmit() {
      this.isSubmit = true;
      const result = this.questionList.every(question => {
        return question.selected === question.answer;
      });
      if (result) {
        this.$store.commit('setLuckyDrawCount', this.$store.state.luckyDrawCount + 1)
        this.$dialog.alert({
          title: "提示",
          message: "恭喜全部答对，去抽奖"
        }).then(() => {
          // this.$emit('route')
          this.$router.push('/luckywheel')
        })
      } else {
        this.$dialog.alert({
          title: "提示",
          message: "未全部答对，可重新作答"
        })
      }
    }
  }
};
</script>

