      background-color: transparent;
    }
    .van-radio {
      margin: 30px 0;
      ::v-deep .van-radio__label {
        font-size: 30px;
        color: #544429;
      }
      ::v-deep .van-icon {
        border: 1px solid #ff8c27;
      }
      ::v-deep .van-radio__icon--checked .van-icon {
        background-color: #ffa500;
      }
      ::v-deep .van-radio__icon--disabled .van-icon {
        background-color: #f3c68a;
      }
      ::v-deep .van-radio__icon--disabled.van-radio__icon--checked .van-icon {
        color: #ebebeb;
      }
    }
    .selected-option {
      ::v-deep .van-radio__label {
        font-weight: 500;
        color: red;
      }
    }
    .answer-option {
      ::v-deep .van-radio__label {
        font-weight: 500;
        color: green;
      }
    }
  }
  .van-button--info {
    background-color: #fa9419;
    border: 1px solid #fa9419;
  }
  .van-button--info {
    background-color: #ee5a0a;
    border: 1px solid #ee5a0a;
  }
}
</style>
